import React from "react";
import PropTypes from "prop-types";

export default function Tab({ children,value }) {
  const getBgColor = value===1 ? '#8fc0a9ff' : (value===2 ? '#68b0abff' : '#4a7c59ff')

  return <div className='category' style={{backgroundColor:value==='1' ? '#8fc0a9ff' : (value==='2' ? '#68b0abff' : '#4a7c59ff')}}>{children}</div>;
}

Tab.propTypes = {
  value: PropTypes.string,
  header: PropTypes.node.isRequired,
  children: PropTypes.node
};